:root {
  --textColor: #06070A;
  --white: #ffffff;
  --light-color: #CACEDC;
  --light-color2: #F7F8FA;
  --light-color3: #A4AAC2;
  --light-color4: #EDEFF4;
  --light-color5: #D5D8E3;
  --light-block: #F2F4F7;
  --footer-color: #222837;
  --light-blue: #505B75;
  --light-blue-third: #5C6987;
  --light-blue-forth: #8C93B2;
  --light-blue-fifth: #343C4D;
  --light-blue-sixth: #7384AA;
  --dark-blue: #12161E;
  --yellow: #FDDA25;
  --yellow-sec: #FFE353;
  --yellow-third: #E5B700;
  --yellow-fourth: #D5A001;
  --yellow-fifth: #F4C400;
  --light-yellow: #FEEFA2;
  --yellow-six: #906400;
}

html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

@font-face {
  font-display: swap;
  font-family: "Roboto";
  src: local("Roboto-Regular"), url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  src: local("Roboto-Medium"), url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  src: local("Roboto Bold"), url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 26px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  color: var(--textColor);
  font-style: normal;
  background: var(--white);
}

main {
  flex-grow: 1;
  background-color: var(--white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body {
  position: relative;
}

.socials {
  display: grid;
  grid-auto-flow: column;
  column-gap: 16px;
  justify-content: center;
}

.socials__link {
  display: contents;
  color: var(--light-block);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.socials__link:hover, .socials__link:focus {
  color: var(--yellow);
}
.socials__link:active {
  color: var(--yellow);
  cursor: grabbing;
}

.socials__svg {
  width: 26px;
  height: 26px;
}

.header {
  position: relative;
  color: var(--white);
  background-color: var(--footer-color);
}

.header__container {
  padding-top: 15px;
  padding-bottom: 17px;
  padding-right: 72px;
}

.header__logo {
  margin-right: auto;
  width: 93px;
  height: 38px;
}

.header__menu {
  display: grid;
  align-items: center;
}

.header__switchers {
  display: flex;
  width: 100%;
  padding-right: 72px;
  padding-left: 23px;
  padding-top: 18px;
  padding-bottom: 20px;
  margin-bottom: 35px;
}

.header__register {
  justify-self: center;
  margin-bottom: 36px;
}

.header__enter {
  justify-self: center;
}

.header__socials {
  justify-self: center;
}

.header__select-wrapper {
  position: relative;
}

.header__select-wrapper--lang .select {
  padding-left: 34px;
}

.header__sel-wrapper--curr {
  margin-right: 22px;
}

@media (max-width: 991px) {
  .header__menu-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    display: grid;
    visibility: hidden;
    grid-template-rows: auto auto auto 1fr auto;
    justify-content: center;
    width: 320px;
    min-height: 100vh;
    padding-bottom: 50px;
    background-color: var(--footer-color);
    transition: visibility 0.3s ease-in-out;
  }

  .header__opener {
    position: absolute;
    top: 25px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 18px;
    height: 18px;
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;
    cursor: pointer;
  }
  .header__opener > span {
    position: relative;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;
  }
  .header__opener > span span {
    position: absolute;
    top: 2px;
    left: 0;
    display: block;
    width: 18px;
    height: 2px;
    background-color: var(--light-color);
  }
  .header__opener > span::after, .header__opener > span::before {
    content: "";
    position: absolute;
    top: 7px;
    left: 0;
    display: block;
    width: 18px;
    height: 2px;
    background-color: var(--light-color);
  }
  .header__opener > span::after {
    top: 12px;
  }
  .header__opener:hover, .header__opener:focus {
    opacity: 0.8;
  }
  .header__opener:active {
    cursor: grabbing;
  }

  .opened .header__opener span {
    background-color: transparent;
  }
  .opened .header__opener span::after {
    top: 0;
    background: var(--light-blue-forth);
    transform: rotate(45deg);
  }
  .opened .header__opener span::before {
    top: 0;
    background: var(--light-blue-forth);
    transform: rotate(135deg);
  }

  .opened .header__menu-wrapper {
    right: 0;
    visibility: visible;
  }

  .header__bell {
    display: none;
  }

  .header__details {
    display: none;
  }
}
.header__btn svg {
  display: block;
}

@media (min-width: 992px) {
  .header__container {
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .header__menu-wrapper {
    display: flex;
  }

  .header__switchers {
    padding-top: 0;
    padding-bottom: 0;
    padding-right: 18px;
    margin-bottom: 0;
  }

  .header__register {
    margin-bottom: 0;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .header__bell {
    margin-right: 36px;
    width: 32px;
    height: 32px;
    color: var(--footer-color);
    background-color: var(--light-block);
    box-shadow: -2px -4px 10px rgba(80, 91, 117, 0.1), 2px 4px 10px rgba(6, 7, 10, 0.5), -2px -4px 10px rgba(80, 91, 117, 0.5), 2px 4px 13px rgba(6, 7, 10, 0.5);
    border-radius: 10px;
  }
  .header__bell svg {
    display: block;
  }

  .header__btn {
    margin-right: 22px;
  }

  .header__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 42px;
    height: 42px;
    color: var(--yellow);
    border-radius: 10px;
    border: 2px solid var(--yellow);
    background: linear-gradient(159.44deg, #505B75 9.89%, #222837 92.84%);
    box-shadow: -2px -4px 13px #505B75, 2px 4px 13px rgba(6, 7, 10, 0.9), -2px -4px 13px #505B75, 2px 4px 18px #06070A;
    cursor: pointer;
  }
  .header__icon:hover, .header__icon:focus {
    background: linear-gradient(159.44deg, #222837 9.89%, #505B75 92.84%);
    box-shadow: -2px -4px 13px #505B75, 2px 4px 13px rgba(6, 7, 10, 0.9), -2px -4px 13px #505B75, 2px 4px 18px #06070A;
  }
  .header__icon:active {
    background: linear-gradient(153.43deg, #12161E 16.22%, #343C4D 53.35%, #5C6987 83.33%);
    box-shadow: -2px -4px 13px #505B75, 2px 4px 13px rgba(6, 7, 10, 0.9), -2px -4px 13px #505B75, 2px 4px 16px #06070A, inset -2px -4px 10px #7384AA, inset 2px 4px 10px #06070A;
    cursor: grabbing;
  }

  .header__details p {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 22px;
  }
}
@media (min-width: 1200px) {
  .header__register {
    margin-right: 36px;
  }

  .header__switchers {
    padding-right: 0;
    margin-right: 36px;
  }
}
.footer {
  color: var(--white);
  background-color: var(--footer-color);
}

.footer__wrapper {
  padding-top: 21px;
  padding-bottom: 21px;
}

.footer__logo {
  width: 93px;
  height: 38px;
}

.footer__socials {
  display: none;
}

.footer__terms {
  text-align: right;
}
.footer__terms p {
  margin: 0;
  font-weight: 400;
  font-size: 13px;
  line-height: 200%;
  letter-spacing: 0.02em;
}

@media (min-width: 960px) {
  .footer__wrapper {
    padding-top: 120px;
    padding-bottom: 120px;
  }
}
.menu__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.menu__link {
  padding-top: 11px;
  padding-bottom: 11px;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.025em;
  color: var(--white);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.menu__link:hover, .menu__link:focus {
  color: var(--yellow);
}
.menu__link:active {
  color: var(--yellow);
  cursor: grabbing;
}

.menu__link--active {
  color: var(--yellow);
}

.btn {
  font-family: inherit;
  padding-top: 11.5px;
  padding-bottom: 11.5px;
  padding-left: 35px;
  padding-right: 35px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  background-color: transparent;
  border-radius: 10px;
  border: 0;
  cursor: pointer;
}
.btn:active {
  cursor: grabbing;
}

.btn--light {
  color: var(--textColor);
  text-shadow: 0.4px 0.4px 0.6px var(--light-yellow);
  background: linear-gradient(153.43deg, var(--yellow-sec) 29.75%, var(--yellow-third) 83.33%);
  box-shadow: -2px -4px 13px var(--light-blue), 2px 4px 13px var(--textColor), -2px -4px 13px var(--light-blue), 2px 4px 18px var(--textColor);
}
.btn--light:hover, .btn--light:focus {
  background: linear-gradient(153.43deg, var(--yellow-third) 0%, var(--yellow-sec) 68.75%);
}
.btn--light:active {
  background: linear-gradient(153.43deg, var(--yellow-fourth) 16.22%, var(--yellow-fifth) 50%, var(--yellow-sec) 83.33%);
  box-shadow: -2px -4px 13px var(--light-blue), 2px 4px 13px rgba(6, 7, 10, 0.9), -2px -4px 13px var(--light-blue), 2px 4px 16px var(--textColor), inset -2px -4px 10px var(--light-yellow), inset 2px 4px 10px var(--yellow-six);
  cursor: grabbing;
}

.btn--dark {
  color: var(--white);
  text-shadow: 0.3px 0.3px 0.6px var(--light-blue-third);
  background: linear-gradient(159.44deg, var(--light-blue) 9.89%, var(--footer-color) 92.84%);
  box-shadow: -2px -4px 13px rgba(80, 91, 117, 0.1), 2px 4px 13px rgba(6, 7, 10, 0.5), -2px -4px 13px var(--light-blue), 2px 4px 18px var(--textColor);
}
.btn--dark:hover, .btn--dark:focus {
  background: linear-gradient(159.44deg, var(--footer-color) 9.89%, var(--light-blue) 92.84%);
}
.btn--dark:active {
  background: linear-gradient(153.43deg, var(--dark-blue) 16.22%, var(--light-blue-fifth) 53.35%, var(--light-blue-third) 83.33%);
  box-shadow: -2px -4px 13px rgba(80, 91, 117, 0.1), 2px 4px 13px rgba(6, 7, 10, 0.5), -2px -4px 13px var(--light-blue), 2px 4px 16px var(--textColor), inset -2px -4px 10px var(--light-blue-sixth), inset 2px 4px 10px var(--textColor);
  cursor: grabbing;
}

.light-switcher__icon {
  width: 19px;
  height: 19px;
}

.light-switcher.day .light-switcher__icon--day {
  display: block;
}
.light-switcher.day .light-switcher__icon--night {
  display: none;
}

.light-switcher {
  width: 32px;
  height: 32px;
  background: var(--footer-color);
  border: 2px solid var(--footer-color);
  box-shadow: -2px -4px 10px rgba(80, 91, 117, 0.1), 2px 4px 10px rgba(6, 7, 10, 0.5), -2px -4px 10px rgba(80, 91, 117, 0.5), 2px 4px 13px rgba(6, 7, 10, 0.5);
  border-radius: 10px;
  cursor: pointer;
}
.light-switcher .light-switcher__icon--day {
  display: none;
}
.light-switcher .light-switcher__icon--night {
  display: block;
}
.light-switcher:hover, .light-switcher:focus {
  background: linear-gradient(159.44deg, #12161E 9.89%, #222837 92.84%);
}
.light-switcher:active {
  cursor: grabbing;
  background: linear-gradient(159.44deg, #12161E 9.89%, #222837 92.84%);
}

.overlay {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 0;
  background: rgba(52, 60, 77, 0.54);
  backdrop-filter: blur(10px);
  visibility: hidden;
  opacity: 0;
  transform: left 0.3s ease-in-out, visibility 0.3s ease-in-out, opacity 0.4s ease-in-out;
}

.overlay--active {
  top: 0;
  left: 0;
  opacity: 1;
  visibility: visible;
}

select::-ms-expand {
  display: none;
}

select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  background-color: transparent;
}

.sel__input {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 13px;
  padding-right: 17px;
  width: 100%;
  font-family: inherit;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.025em;
  border-radius: 10px;
  border: 0;
  cursor: pointer;
}
.sel__input:hover, .sel__input:focus, .sel__input:focus-within {
  outline: 0;
}
.sel__input:active {
  cursor: grabbing;
}

.sel__input--dark {
  color: var(--white);
  text-shadow: 0.3px 0.3px 0.6px var(--light-blue-third);
  background: var(--footer-color);
  border: 2px solid var(--footer-color);
  box-sizing: border-box;
  box-shadow: -2px -4px 10px rgba(80, 91, 117, 0.1), 2px 4px 10px rgba(6, 7, 10, 0.5), -2px -4px 10px rgba(80, 91, 117, 0.5), 2px 4px 13px rgba(6, 7, 10, 0.5);
}
.sel__input--dark:hover, .sel__input--dark:focus, .sel__input--dark:focus-within {
  background: linear-gradient(159.44deg, #12161E 9.89%, #222837 92.84%);
  box-shadow: -2px -4px 10px rgba(80, 91, 117, 0.1), 2px 4px 10px rgba(6, 7, 10, 0.5), -2px -4px 10px rgba(80, 91, 117, 0.5), 2px 4px 13px rgba(6, 7, 10, 0.5);
