}

.sel__input--table {
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0.005em;
  color: var(--textColor);
}

.select__item {
  position: relative;
  display: block;
  width: auto;
}
.select__item input {
  opacity: 0;
}
.select__item input:checked {
  color: var(--yellow);
}
.select__item:hover, .select__item:focus {
  color: var(--yellow);
}

.select__list {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: var(--footer-color);
  border-radius: 10px;
}

.sel {
  position: relative;
}

.sel__label--currency {
