  align-items: center;
  padding-right: 10px;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0.005em;
  color: var(--textColor);
}

.table-aria__select {
  position: relative;
